import { styled, keyframes } from "styled-components";
import { Link } from "react-router-dom";
import logoImg from "../images/common/toplogo_w.png";
import MenuIcon from "@mui/icons-material/Menu";
import { SwipeableDrawer } from "@mui/material";
import Sidebar from "./SwipeBar";
import { useState } from "react";

function Header() {
  const [open, setOpen] = useState(false);
  const [isSubNavOpen, setIsSubNavOpen] = useState(false);
  const [openSidebar, setOpenSidebar] = useState(false);

  const toggleSidebar = (isOpen) => () => {
    setOpenSidebar(isOpen);
  };
  const handleSubNavOpen = () => {
    setIsSubNavOpen(true);
  };

  const handleSubNavClose = () => {
    setIsSubNavOpen(false);
  };

  return (
    <StyledHeader>
      <StyledDiv>
        <Link to="/skyamg">
          <div>
            <img src={logoImg} alt="logo" />
          </div>
        </Link>
      </StyledDiv>
      <StyledUl>
        <li>
          <StyledLink to="/skyamg">SKY </StyledLink>
        </li>
        {/* <li>
          <StyledLink to="/two">two</StyledLink>
        </li> */}
        <li>
          <StyledLink to="/skyamg/skymedicaldoctor"> </StyledLink>
        </li>
        <li>
          <StyledLink to="/skyamg/notice"></StyledLink>
        </li>
        <li>
          <StyledSubUl
            onMouseEnter={handleSubNavOpen}
            onMouseLeave={handleSubNavClose}
          >
            <StyledLink></StyledLink>
            <StyledSubNav open={isSubNavOpen}>
              <li>
                <StyledLink to="/skyamg/subject/1"></StyledLink>
              </li>
              <li>
                <StyledLink to="/skyamg/subject/2"></StyledLink>
              </li>
              <li>
                <StyledLink to="/skyamg/subject/3"></StyledLink>
              </li>
              <li>
                <StyledLink to="/skyamg/subject/4"></StyledLink>
              </li>
              <li>
                <StyledLink to="/skyamg/subject/5"></StyledLink>
              </li>
              <li>
                <StyledLink to="/skyamg/subject/6"></StyledLink>
              </li>
              <li>
                <StyledLink to="/skyamg/subject/7"></StyledLink>
              </li>
              <li>
                <StyledLink to="/skyamg/subject/8"></StyledLink>
              </li>
              <li>
                <StyledLink to="/skyamg/subject/9"></StyledLink>
              </li>
            </StyledSubNav>
          </StyledSubUl>
        </li>
      </StyledUl>
      <StyledIconWrappr onClick={toggleSidebar(true)}>
        <MenuIcon />
      </StyledIconWrappr>
      <Sidebar open={openSidebar} onClose={toggleSidebar(false)} />
    </StyledHeader>
  );
}

const fadeIn = keyframes`
  from {
    opacity: 0;
    transform: translateX(-50%) translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
`;

const StyledHeader = styled.header`
  width: 100%;
  height: 8vh;
  background-color: #101010;
  display: flex;
  justify-content: center;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 999;
  opacity: 0.85;
  /* padding: 0 300px; */
`;

const StyledDiv = styled.div`
  height: 100%;
  position: absolute;
  display: flex;
  align-items: center;
  left: 15px;

  div {
    max-width: 150px;
    height: 100%;
    display: flex;
    align-items: center;
    img {
      width: 100%;
      height: auto;
    }
  }
`;

const StyledUl = styled.ul`
  width: 100%;
  height: 100%;
  list-style-type: none;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0 10vw;

  li {
    margin-right: 5vw;
  }
  @media screen and (max-width: 690px) {
    display: none;
  }
`;

const StyledLink = styled(Link)`
  color: #999;
  text-decoration: none;
  display: inline-block;
  position: relative;
  padding-bottom: 2px;
  height: 8vh;
  display: flex;
  align-items: center;

  &::before {
    content: ""; /*      */
    position: absolute; /*   .text    */
    bottom: 15px; /*     */
    left: 0; /*      */
    width: 0; /*    0 */
    height: 1px; /*    */
    background-color: #dabfa8; /*    */
    transition: width 0.3s ease; /*        */
  }

  &:hover {
    color: #dabfa8;
    /* font-weight: bold; */
  }
  &:hover::before {
    width: 100%; /*      100%  */
  }
`;

const StyledSubUl = styled.ul`
  list-style-type: none;
  position: relative;
  padding: 0;
  margin: 0;
  text-align: center;
`;

const StyledSubNav = styled.nav`
  list-style-type: none;
  position: absolute;
  top: 100%;
  left: 50%; /*   50%    */
  transform: translateX(-50%); /*  50%    */
  background-color: #101010;
  display: ${({ open }) => (open ? "flex" : "none")};
  padding: 0;
  margin: 0;
  width: 130px; /*     */
  border-radius: 6px; /*    */
  flex-direction: column;
  align-items: center;
  animation: ${({ open }) => (open ? fadeIn : "none")} 0.3s ease; /*   */

  li {
    margin: 0;
  }
`;

const StyledIconWrappr = styled.div`
  position: absolute;
  right: 15px;
  color: #999;
  cursor: pointer;
  display: none;

  &:hover {
    color: #dabfa8;
  }

  @media screen and (max-width: 690px) {
    display: block;
  }
`;

export default Header;
